import React,{Component} from 'react';

function Choice(props){
    return(
        <div className='max-w-max'>
            <div className='flex flex-row border-2 group focus-within:border-red-700 transition ease-in-out rounded-md'>
                <input type='text' value={props.choice} onChange={props.onChange} className="w-full px-5 py-2 rounded-md outline-none flex-grow "/>
                <button onClick={props.choiceDelete} className="text-red-600 align-middle p-1 focus:outline-none">
                    <svg  className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"></path></svg>
                </button>
            </div>
        </div>
    )
}
class MultiChoice extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...this.props.data
        }
        this.changeHandle = this.changeHandle.bind(this);
        this.addChoice = this.addChoice.bind(this);
        this.choiceDeleteHandler = this.choiceDeleteHandler.bind(this);
    }
    
    componentDidUpdate(prevProps,prevState){
        if(JSON.stringify(prevState) !== JSON.stringify(this.state))
            this.props.onUpdate(this.props.childIndex, this.state);
    }
    
    changeHandle(e, index){
        const newState = {...this.state}

        if(index === -1)
            newState.questionText = e.target.value
        else
            newState.choices[index] = e.target.value

        this.setState(newState);
    }

    addChoice(){
        const choices = this.state.choices.slice();
        choices.push("New Choice");
        this.setState({choices:choices});
    }

    choiceDeleteHandler(e,index){
        const choices = this.state.choices.slice();
        const newChoiceList = choices.slice(0,index).concat(choices.slice(index+1))
        this.setState({choices:newChoiceList})
    }

    render(){
        return(
            <div className=''>
                <input type='text' value={this.state.questionText} className="w-full px-5 py-3 border-2 rounded-md focus:border-red-700 transition-colors ease-in-out outline-none" placeholder='Question Text' onChange={(e)=> this.changeHandle(e, -1)}/>
                <div className='space-y-3 mt-3 '>
                    { this.state.choices.map( (choiceText, choiceIndex) => <Choice 
                                                                                key={choiceIndex} 
                                                                                choice={choiceText} 
                                                                                onChange={(e) => this.changeHandle(e,choiceIndex)} 
                                                                                choiceDelete={(e) => this.choiceDeleteHandler(e,choiceIndex)}
                                                                            />)}
                    <button onClick={this.addChoice} className="font-semibold px-4 py-2 border text-red-700 rounded-md border-red-700 transition-colors hover:bg-red-700 hover:text-white "> New Choice </button>
                </div>
            </div>
        );
    }

}

export default MultiChoice