import { Component } from "react";
import { BrowserRouter, Switch, Route, Link } from "react-router-dom";
import Login from "./components/login";
import  DynamicFormCreator  from "./components/DynamicFormCreator/dynamic_form_creator";
import  FormList  from "./components/FormList/form_list";
import  FillForm  from "./components/FillForm/fill_form";
import  ViewResponse  from "./components/VIewResponse/view_response";
import  YSBH  from "./components/ysbh";

class App extends Component{
    constructor(props){
        super(props)
        this.state ={
            username:sessionStorage.getItem('username'),
            token:sessionStorage.getItem('token'),
            tokenEndTime:sessionStorage.getItem('tokenEndTime'),
            userType:sessionStorage.getItem('userType'),
            tokenCheckerId:null,
        }
        this.setUserDetails = this.setUserDetails.bind(this)
        this.logout = this.logout.bind(this);
        this.tokenChecker = this.tokenChecker.bind(this);
    }

    tokenChecker(){
        const now  = new Date()
        const end = new Date(Number(this.state.tokenEndTime))
        if ( now > end)
        {
            this.logout()
        }
    }

    componentDidMount(){
        if(this.state.token !== null)
        {

            const id = setInterval(this.tokenChecker, 1000);
            this.setState({
                tokenCheckerId:id
            })
        }
    }
    
    setUserDetails( token, tokenDuration, username, usertype){

        const now = new Date();
        const endTime = now.getTime() + tokenDuration*1000
        const tokenEndTime = new Date( endTime ).getTime();
        const id = setInterval(this.tokenChecker, 1000);

        this.setState({
            token:token,
            username:username,
            tokenEndTime:tokenEndTime,
            userType:usertype,
            tokenCheckerId:id
        })

        sessionStorage.setItem("token", token)
        sessionStorage.setItem("username", username)
        sessionStorage.setItem("tokenEndTime", tokenEndTime)
        sessionStorage.setItem("userType", usertype)
    }

    logout(){
        clearInterval(this.state.tokenCheckerId)

        this.setState({
            token:null,
            username:null,
            tokenEndTime:null,
            userType:null,
            tokenCheckerId:null,
        })
        sessionStorage.clear()
    }
    render(){
        if( this.state.token === null)
            return <Login setToken={this.setUserDetails}/>
        
        if(this.state.userType === 'user')
            return(
                <BrowserRouter>
                    <Switch>
                        <Route path="/form/:id/fill">
                            <FillForm token={this.state.token} />
                        </Route>
                        <Route path="/">
                            <YSBH logout={this.logout}/>
                        </Route>
                    </Switch>
                </BrowserRouter>
            )
        return(
            <BrowserRouter>
                <nav className="p-3 text-lg bg-red-700 text-white font-bold space-x-12 flex flex-row justify-center">
                    <Link to="/create">Create Form</Link>
                    <Link to="/">Home</Link>
                    <Link to="/" onClick={this.logout}>Logout</Link>
                </nav>
                <Switch>
                    <Route path="/create">
                        <DynamicFormCreator token={this.state.token} />
                    </Route>
                    <Route path="/form/:id/responses">
                        <ViewResponse token={this.state.token}/>
                    </Route>
                    <Route path="/form/:id/fill">
                        <FillForm token={this.state.token} />
                    </Route>
                    <Route path="/">
                        <FormList token={this.state.token} />
                    </Route>
                </Switch>
            </BrowserRouter>
        );
    }

}

export default App