import React, { Component } from "react";
import { withRouter } from "react-router";

function QA(props){
    return(
        <div>
            <h2 className='font-semibold'>{props.index+1} ) {props.question} </h2>
            <p>Ans. {props.answer} </p>
        </div>
    )
}
function FormResponse(props){
    const QAs = props.submission_data.answers.map((ans, ind) => <QA key={ind} index={ind} question={props.questions[ind].questionText} answer={ans}/>)
    return(
        <div className='p-8 space-y-4 rounded-md shadow-md hover:scale-105 hover:shadow-lg ease-in-out transition transform'>
            <h1 className='text-3xl text-red-700 font-semibold'>Username: {props.username}</h1>
            <div className='pl-8'>
                {QAs}
            </div>
        </div>
    )
}
class ViewResponse extends Component{
    constructor(props){
        super(props);
        this.state = {
            form_responses:[],
            questions:[],
        }
    }
    componentDidMount(){
        const base64 = require("base-64");
        const form_id = this.props.match.params.id;
        const headers = new Headers();
        headers.append("Authorization","Basic "+base64.encode(this.props.token+":boo"))
        
        fetch(`/api/form/${form_id}/submissions`,{
            methods:"GET",
            headers:headers,       
        })
        .then( d => d.json())
        .then(data => {
            if(data.form_submissions.length)
                this.setState({
                    form_responses:data.form_submissions,
                    questions:data.questions
                })
        })
    }
    render(){
        const FormResponses = this.state.form_responses.map( (data, index) => <FormResponse key={index} {...data} questions={this.state.questions}/>)
        return(
            <div className='max-w-6xl mt-8 space-y-8 mx-auto'>
                <h1 className='text-5xl p-3 border-b border-black font-bold text-center'>Form Responses</h1>
                {FormResponses}
            </div>
        );
    }
}

export default withRouter(ViewResponse)