import React, { Component } from "react";
import { withRouter } from "react-router";

function SimpleQuestion(props){
    
    return(
    <div className='flex flex-col space-y-2'>
        <h2 className='font-semibold'>{(props.index + 1) +")"+ props.data.questionText}</h2>
        <input className='border-2 px-5 py-3 outline-none rounded-md focus:border-red-700 focus:border' value={props.value} onChange={e => props.changeHandler(e.target.value,props.index)}/>
    </div>
    );
}

function Choice(props){
    return(
            <button onClick={(e) => props.changeHandler(props.choice,props.index)} className={` text-left px-5 py-3 border border-red-700 hover:bg-red-700 hover:text-white rounded-md ${ props.activeChoice===props.choice ? "bg-red-700 text-white" : ""}`}>
                {props.choice}
            </button>
        
    )
}
function FormSubmitLoading(props){
    return(
        <div>
            
        </div>
    )
}

function FormSubmitted(props){
    return(
        <div className='text-center p-8 rounded-md my-32 max-w-xl mx-auto bg-red-700'>
            <h1 className='text-4xl font-bold text-white'>
                Form Submitted.
            </h1>
        </div>
    );
}

function MultiChoiceQuestion(props){
    const choices = props.data.choices.map( (choice,index) => <Choice choice={choice} 
                                                                      key={index} 
                                                                      index={props.index}
                                                                      activeChoice={props.activeChoice} 
                                                                      changeHandler={props.changeHandler}/>)
    return(
    <div className="flex flex-col space-y-2">
        <h2 className='font-semibold'>{(props.index + 1) +")"+ props.data.questionText}</h2>
        <div className=' flex flex-col space-y-2 max-w-max'>
            {choices}
        </div>
    </div>);
}

class FillForm extends Component{
    constructor(props){
        super(props);
        this.state = {
            formData: null,
            answers: [],
            isLoading:true,
            viewState:"default",
        }
        this.changeHandler = this.changeHandler.bind(this);
        this.componetize = this.componetize.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    componentDidMount(){
        const token = this.props.token;
        const id = this.props.match.params.id
        this.setState({
            id:id
        })
        const headers = new Headers();
        const base64 = require('base-64')
        headers.append("Authorization", "Basic "+base64.encode(token+":boo"))
        
        fetch("/api/form/get/"+id,{
            method:"GET",
            headers:headers
        })
        .then( d => d.json())
        .then( d => this.setState({
            formData:{...d},
            isLoading:false,
            answers:new Array(d.formEntries.length)
        }))
    }

    componetize(formEntry, index){
        switch(formEntry.type){
            case "simple": return <SimpleQuestion key={index} index={index} value={this.state.answers[index]} data={formEntry} changeHandler={this.changeHandler}/>
            case "multi-choice": return <MultiChoiceQuestion key={index} index={index} data={formEntry} activeChoice={this.state.answers[index]} changeHandler={this.changeHandler}/>
            default: return <h1>Dumb Shit Happend</h1>
        }
        
    }

    changeHandler(answer,index){
        const answers = this.state.answers
        answers[index] = answer
        this.setState({
            answers:answers
        })
    }

    handleSubmit(){
        const answers = {answers:this.state.answers}
        const id = this.state.id
        const headers = new Headers()

        const base64 = require("base-64")
        headers.append("Authorization", "Basic "+ base64.encode(`${this.props.token}:boo`))
        headers.append("Content-Type","application/json")

        fetch(`/api/form/fill/${id}`,{
            method:"POST",
            headers:headers,
            body:JSON.stringify(answers)
        })
        .then( d => d.json())
        .then(d => {
            if(d.msg == "added succesfully")
            {
                this.setState({
                    viewState:"submitted"
                })
            }
        })

    }

    render(){
        if(this.state.isLoading)
            return <h1>Loading</h1>
        if(this.state.viewState === "submitted")
            return <FormSubmitted/>
        const formEntries = this.state.formData.formEntries.map(this.componetize)
        return(
            <div className='w-full min-h-screen bg-red-700 py-8'>
                <div className=' bg-white max-w-4xl mx-auto z-10 p-8 border rounded-md'>
                    <div>
                        <h1 className="text-4xl font-extrabold"> {this.state.formData.heading} </h1>
                        <h4 className="font-light text-md text-gray-500"> {this.state.formData.description} </h4>
                    </div>
                    <div className='flex flex-col space-y-8 py-8'>
                        {formEntries}
                    </div>
                    <button className="px-7 py-3 bg-red-700 text-white rounded-md w-full font-bold" onClick={this.handleSubmit}>Submit</button>
                </div>
            </div>
        )
    }
}

export default withRouter(FillForm);