import { React, Component } from "react";

class SimpleQuestion extends Component {
    constructor(props){
        super(props)
        
        this.state = {
            ...this.props.data
        }

        this.handleChange = this.handleChange.bind(this);
    }

    componentDidUpdate(prevProps,prevState){
            if(JSON.stringify(prevState) !== JSON.stringify(this.state))
                this.props.onUpdate(this.props.childIndex, this.state);
    }

    handleChange(e){
        this.setState({
            questionText:e.target.value
        })
    }

    render(){
        return(
                <input type='text' value={this.state.questionText} className='w-full px-5 py-3 border-2 rounded-md focus:border-red-700 transition-colors ease-in-out outline-none' onChange={this.handleChange}/>
        );
    }
}
export default SimpleQuestion