import { Component } from "react";
import { Link } from "react-router-dom";
import NoFormCreated from "../NoFormCreated";
function FormEntry(props){
    return(
        <div className='p-6 shadow-md rounded-lg hover:scale-105 transform transition hover:shadow-lg'>
            <div>
                <h3 className='text-2xl font-extrabold'>{props.form_name}</h3>
                <p className='font-light text-sm text-gray-800'> {props.form_desc} </p>
            </div>

            <h1 className='font-semibold'>
                Form Link: <Link className="font-base text-red-700 hover:underline" to={`/form/${props.form_id}/fill`}>{window.location.host+`/form/${props.form_id}/fill`}</Link>
            </h1>

            <div className='mt-6 flex flex-row space-x-6'>
                <Link className="px-3 py-2 font-bold border border-red-700 text-red-700 rounded-lg hover:bg-red-700 hover:text-white transition-colors ease-in-out" to={`/form/${props.form_id}/responses`}>
                    Responses
                </Link>
            </div>
        </div>
    )
}
class FormList extends Component{
    constructor(props){
        super(props);
        this.state = {
            isLoading:true,
            form_entries:[

            ]
        }
    }
    componentDidMount(){
        const token = this.props.token;
        const headers = new Headers();
        let base64 = require("base-64");
        headers.append("Authorization","Basic "+base64.encode(token+":boo"))
        headers.append("Content-Type","application/json")

        fetch("/api/form/all",{
            method:"GET",
            headers:headers
        })
        .then(d => d.json())
        .then( data => {    
            if( data.length > 0)
            {
                const form_entries = data
                this.setState({
                    form_entries:form_entries,
                    isLoading:false,
                })
            }
        })
    }

    render(){
        if(this.state.isLoading)
            return <h1> Loading </h1>
        const FormEntryComponents = this.state.form_entries.map((form_entry, index) => <FormEntry key={index} form_id={form_entry.id} form_name={form_entry.heading} form_desc={form_entry.description} />)
        return(
            <div className="max-w-4xl py-8 mx-auto space-y-8 ease-in-out transition transform">
              { FormEntryComponents.length ?  <h1 className='text-4xl font-extrabold border-b pb-2 border-black text-center'>
                    Your Forms
                </h1> : ""}
                { FormEntryComponents.length > 0 ? FormEntryComponents : <NoFormCreated/> }
            </div>
        );
    }
}

export default FormList;